// Pedido.js
import React from 'react'
import { useLocation, Navigate } from 'react-router-dom'
import '../styles/Pedido.css'
import Side from '../components/Side'


const Pedido = () => {
  const location = useLocation()
  const pedidoData = location.state

  if (!pedidoData) {
    // Redirecionar de volta se no houver dados no estado da localizao
    return <Navigate to="/" />
  }

  return (
    <div class="divPedido">
      <div class="divCabecalho">
        <h1>Ol {pedidoData.nome}! Escolha seu pedido</h1>
      </div>
      <div class="divLinha" />
      
      <div class="divTipos">
        <button>Churrasco</button>
        <button>Acompanhamento</button>
        <button>Batata Frita</button>
        <button>Bebida</button>
      </div>
      <div class="divMenu">
        <div class="divOpc">
          <div class="divTipos2">
          <Side/>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Pedido
